
img{
    border:none;
}
.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.main{
    font-size: 50px;
    font-weight: bold;
    background: linear-gradient(to right, rgb(247, 85, 255), rgb(58, 150, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    margin:0;
    margin-top: 0.5cm;
}

.game{
    display: flex;
    flex-direction: column;
    row-gap: 0.5cm;
}

.row1, .row2, .row3{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
}
.b1{
    height:1cm;
    width:1cm;
    padding:0.5cm;
    border-radius: 4px;
    border: none;
    background-color: rgb(255, 255, 255);
    box-shadow: 0.5px 0.5px 2px 2px rgb(98, 98, 98);
    transition:2s ease;
}

.heading{
    font-size: 24px;
    color:rgb(132, 53, 206);
    text-shadow: 1px 1px plum;
    font-weight: bolder;
}

.resultp{
    font-size: 20px;
    color:rgb(114, 4, 4);
    font-weight: bold;
}

.restartButton{
    width:1cm;
    height:1cm;
}

@media only screen and (max-width:400px){
    .b1{
        height:0.8cm;
        width:0.8cm;
        padding:0.2cm;
    }
    .main{
        font-size: 35px;
    }
}