.create-container {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    backdrop-filter: blur(10px);
    width: 100%;
    display: flex;
    justify-content: center;

}

.create-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.close {
    width: .4cm;
    cursor: pointer;
}

.create-box {
    width: 8cm;
    border: 1px solid black;
    background-color: rgb(223, 247, 255);
    padding: .2cm .5cm .5cm .5cm;
    height: fit-content;
    margin-top: 2cm;
}

.create-button {
    padding: .2cm;
    background-color: rgb(43, 82, 201);
    color: white;
    font-size: 18px;
    cursor: pointer;
}

.room {
    cursor: pointer;
    width: fit-content;
    padding: .3cm .8cm;
    background-color: rgb(227, 227, 227);
    border: 1px solid rgb(183, 183, 183);
}

.rooms-box {
    display: flex;
    flex-wrap: wrap;
    column-gap: .4cm;
    row-gap: .4cm;
}

.room-name {
    font-family: raleway;
    font-weight: 600;
    font-size: 18px;
}

#createBox {
    display: none;
}

.input-room-name {
    padding: .2cm;
    font-size: 18px;
}

.create-form {
    margin-top: .2cm;
    display: flex;
    flex-direction: column;
    row-gap: .2cm;

}